body {
  font-family: "Poppins";
  color: #4a6f8a;
  background-color: #ffffff;
  font-size: 15px;
}

/*-- Default CSS --*/
body {
  font-family: "Poppins";
  color: #4a6f8a;
  background-color: #ffffff;
  font-size: 15px;
}
h1,h2,h3,h4,h5,h6 {color: #232323;}
p {line-height: 1.7;}
a {transition: 0.5s all ease;text-decoration: none; color: #0046c0;}
a:hover {color: #111111;}
img {max-width: 100%;}
.d-table {width: 100%;height: 100%;}
.d-table-cell {vertical-align: middle;}
.ptb-100 {padding-top: 100px;padding-bottom: 100px;}
.pt-100 {padding-top: 100px;}
.pb-70 {padding-bottom: 70px;}
.pb-100 {padding-bottom: 100px;}
button:focus {outline: 0;}
.btn.focus,.btn:focus {box-shadow: none;}
/*-- End Default CSS --*/

/*-- Header Top --*/
.header-top {
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #f2f2f2;
  background-color: #ffffff;
  z-index: 1;
}

.header-top-item .header-top-left ul {
  margin: 0;  padding: 0;
}

.header-top-item .header-top-left ul li {
  display: inline-block;
  list-style-type: none;
  font-weight: 500;
  font-size: 14px;
  color: #4a6f8a;
  margin-right: 25px;
}

.header-top-item .header-top-left ul li i {
  color: #0046c0;
  font-size: 18px;
  position: relative;
  top: 2px;
  margin-right: 2px;
}

.header-top-item .header-top-left ul li:last-child {
  margin-right: 0;
}

.header-top-item .header-top-left ul li a {
  display: inline-block;
  color: #4a6f8a;
}

.header-top-item .header-top-left ul li a:hover {
  color: #0046c0;
}

.header-top-item .header-top-left ul li a i {
  color: #0046c0;
  font-size: 18px;
  position: relative;
  top: 2px;
  margin-right: 2px;
}

.header-top-item .header-top-right {
  text-align: right;
}

.header-top-item .header-top-right ul {
  margin: 0;  padding: 0;
}

.header-top-item .header-top-right ul li {
  list-style-type: none;
  display: inline-block;
  margin-right: 5px;
}

.header-top-item .header-top-right ul li:last-child {
  margin-right: 0;
}

.header-top-item .header-top-right ul li a {
  display: block;
  width: 25px;
  height: 25px;
  line-height: 25px;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  border: 1px solid transparent;
  background-color: #0046c0;
}

.header-top-item .header-top-right ul li a:hover {
  color: #0046c0;
  background-color: transparent;
  border: 1px solid #0046c0;
}

/*-- End Header Top --*/

/*-- Navbar --*/
.mobile-nav {display: none;}
/* .navbar-light .navbar-brand,.navbar-light .navbar-brand:hover {width: 95px;} */
.navbar-light .navbar-brand{
  color: #4a6f8a;
}
.navbar-brand{
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.8rem;
  color: #4a6f8a;
}
.navbar-brand i{
  color: #4a6f8a;
  font-size: 72px;
  position: relative;
  top: 0;
  margin-right: 22px;
}

#navbarSupportedContent{justify-content: flex-end;}
.main-nav {
  background: #ffffff;
  padding-top: 0;
  padding-bottom: 0;
  position: relative;
}

.main-nav nav {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 0;
  padding-left: 0;
  transition: 0.5s all ease;
}

/* .main-nav nav .navbar-nav {margin-right: auto; margin-left: auto;} */

.main-nav nav .navbar-nav .nav-item:hover a {
  color: #0046c0;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu {
  background: #ffffff;
  box-shadow: 0px 0px 15px 0px #ddd;
  border: 0;
  border-left: 3px solid #0046c0;
  padding-top: 15px;
  padding-bottom: 15px;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li {
  border-bottom: 1px dashed #0046c047;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li:last-child {
  border-bottom: 0;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li a {
  color: #4a6f8a;
  font-size: 15px;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:hover,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:focus,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a.active {
  color: #0046c0;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover a {
  color: #0046c0;
}

.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover a:hover,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover a:focus,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover a.active {
  color: #0046c0;
}
.main-nav nav .navbar-nav .nav-item a {
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
  color: #fff;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-left: 20px;
  margin-right: 20px;
}

.main-nav nav .navbar-nav .nav-item a {
  font-weight: 500;
  font-size: 16px;
  color: #4a6f8a;
  text-transform: capitalize;
}


.main-nav nav .navbar-nav .nav-item a:hover,
.main-nav nav .navbar-nav .nav-item a:focus,
.main-nav nav .navbar-nav .nav-item a.active {
  color: #0046c0;
}


.menu-shrink {
  box-shadow: 0px 0px 15px 0px #ddd;
}

.menu-shrink nav {
  padding-top: 10px;
  padding-bottom: 10px;
}
/*-- End Navbar --*/

/*-- Home Slider --*/
.slider-item-img {
  background-image: url(/drmridul/static/media/slider-bg-01.076cbdb2.jpg);
}

.slider-item {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 730px;
  position: relative;
}

.slider-item .slider-shape img {
  position: absolute;
  top: -9px;
  right: -90px;
  max-width: 730px;
}

.slider-item .slider-shape-two img {
  position: absolute;
  top: 20px;
  right: -60px;
  max-width: 730px;
}

.slider-item .slider-shape-three img {
  position: absolute;
  top: 60px;
  right: 0;
  max-width: 730px;
}

.slider-item:before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #0046c0;
  opacity: .80;
}

.slider-item .slider-text {
  position: relative;
  margin-top: -160px;
}

.slider-item .slider-text h1 {
  font-weight: 700;
  font-size: 46px;
  color: #ffffff;
  margin-bottom: 35px;
  max-width: 600px;
  margin-left: 0;
}

.slider-item .slider-text p {
  color: #ffffff;
  font-weight: 40;
  margin-bottom: 40px;
  max-width: 575px;
  margin-left: 0;
}

.common-btn a {
  display: inline-block;
  color: #0046c0;
  background-color: #ffffff;
  font-size: 15px;
  font-weight: 500;
  padding: 12px 18px;
  border-radius: 5px;
  margin-right: 20px;
  border: 2px solid transparent;
}

.common-btn a:hover {
  background-color: transparent;
  border: 2px solid #ffffff;
  color: #ffffff;
}

.common-btn a:last-child {
  margin-right: 0;
}

.common-btn .cmn-btn-right {
  background-color: transparent;
  color: #ffffff;
  border: 2px solid #ffffff;
  padding-left: 25px;
  padding-right: 25px;
}

.common-btn .cmn-btn-right:hover {
  color: #0046c0;
  background-color: #ffffff;
}

.home-slider .owl-prev {
  position: absolute;
  top: 45%;
  left: 15px;
  width: 55px;
  height: 55px;
  line-height: 55px !important;
  font-size: 28px !important;
  border-radius: 50% !important;
  color: #ffffff !important;
  background-color: #2459b8 !important;
  transition: 0.5s all ease;
}

.home-slider .owl-prev:hover {
  color: #ffffff !important;
  background-color: #111111 !important;
}

.home-slider .owl-next {
  position: absolute;
  top: 45%;
  right: 15px;
  width: 55px;
  height: 55px;
  line-height: 55px !important;
  font-size: 28px !important;
  border-radius: 50% !important;
  color: #ffffff !important;
  background-color: #2459b8 !important;
  transition: 0.5s all ease;
}

.home-slider .owl-next:hover {
  color: #ffffff !important;
  background-color: #111111 !important;
}

.home-slider.owl-theme .owl-dots {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 150px;
}

.owl-theme .owl-nav {
  margin-top: 0;
}

.home-slider.owl-theme .owl-dots .owl-dot span {
  width: 20px;
  height: 5px;
  margin: 5px 4px;
  background-color: #0046c0;
  transition: 0.5s all ease;
}

.home-slider.owl-theme .owl-dots .owl-dot.active span,
.home-slider.owl-theme .owl-dots .owl-dot:hover span {
